.cart{

  &__bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.4rem;
    font-weight: 600;
    padding: 2rem 0;
    text-align: center;
  }
  &__bar-item{
    width: 5.25rem;
  }
  &__coupons{

  }
  &__input{
    border: none;
    border-radius: 5px;
    padding: 1rem .5rem;
    outline: none;
    box-shadow: 0px 0px 3px 1px rgba(0,0,0,0.75);
  }
  &__added{
    margin-left: 2rem;
    font-weight: 700;
    color: rgb(0, 148, 37);
  }
  &__summary{
    min-width: 15rem;
    margin: 1rem 0 0 auto;
    text-align: right;

  }
  &__info{
    text-align: center;
    margin: 3rem 0;
    font-size: 3rem;
    font-weight: 700;
  }
  &__buttons{
  }
  &__btn{
    display: inline-block;
    border: none;
    padding: .5rem 1rem;
    border-radius: 10px;
    color: #fff;
    background: rgb(0, 148, 37);
    margin: 1rem;
    cursor: pointer;
  }
  &__btn-danger{
    background: crimson;
  }

  @media(max-width: 490px){

    &__bar{
      display: none;
    }
    &__added{
      display: block;
      margin: 1rem 0;
    }
  }

  @media(max-width: 420px){
    &__coupons{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    &__summary{
      margin: 1rem auto;
      text-align: center;
    }
    &__added{
      text-align: center;
    }
  }

}